<template>
  <v-container
  id="payment"
  fluid
  tag="section"
  >
    <v-row>
      <v-col
        v-for="({ actionIcon, actionText, ...attrs }, i) in stats"
        :key="i"
        cols="12"
        md="12"
        lg="3"
      >
        <material-stat-card v-bind="attrs">
          <template #actions>
            <v-icon
              class="mr-2"
              small
              v-text="actionIcon"
            />
            <div class="text-truncate">
              {{ actionText }}
            </div>
          </template>
        </material-stat-card>
      </v-col>
      <v-col
        cols="12"
        md="12"
      >
        <material-card
          color="primary"
          full-header
        >
          <template #heading>
            <div class="pa-8 white--text">
              <div class="mvp hidden z-30 left-0 -translate-x-6 md:-translate-0 absolute md:left-1/3 w-screen md:w-1/3">
                  <material-card
                    color="white"
                    full-header
                  >
                    <template #heading>
                      <div class="flex justify-between mx-4 relative p-6">
                        <h3 class="text-gray-800 text-sm">New +</h3>
                          <v-icon
                            @click="ssclose"
                            color="error"
                            class="hover:opacity-30"
                          >
                            fa-solid fa-rectangle-xmark
                          </v-icon>
                      </div>
                    </template>
                    <v-card-text>
                      <v-row>
                        <v-col
                          cols="12"
                          md="12"
                        >
                          <v-combobox
                            v-model="name"
                            :items="gap"
                            label="Fullname*"
                            append-icon="mdi-account"
                            clearable
                            filled
                            multiple
                          />
                            <v-text-field
                            v-model="tagihan"
                            type="number"
                            label="Jumlah Pembayaran*"
                            append-icon="fa-solid fa-dollar"
                            filled
                          />
                        </v-col>
                        <v-col
                        cols="12"
                        md="12"
                        >
                          <v-btn
                            @click="svbtn"
                            elevation-2
                            class="uppercase w-full np"
                            color="indigo lighten-2"
                            text
                          >
                            save
                          </v-btn>
                        </v-col>
                      </v-row>
                    </v-card-text>
                  </material-card>
              </div>
              <div class="g hidden w-screen md:w-1/2 absolute left-0 z-30 md:left-1/4 -translate-x-6 md:-translate-x-10">
                <material-card
                  color="accent"
                  full-header
                >
                    <template #heading>
                      <div class="w-full">
                        <div class="absolute subheading right-7 -translate-y-4">
                          <v-btn
                            @click="sssclose"
                            color="red"
                            small
                          >
                          <v-icon
                            class="mx-1"
                            small
                          >
                          fa-solid fa-rectangle-xmark
                          </v-icon>
                            Close
                          </v-btn>
                        </div>
                        <v-tabs
                          v-model="tabs"
                          background-color="transparent"
                          slider-color="white"
                          class="pa-2"
                        >
                          <span
                            class="subheading font-weight-light mx-3"
                            style="align-self: center"
                          >Clone from:</span>
                          <v-tab class="mr-3" small>
                            <v-icon class="mr-2">
                              fa-solid fa-cloud-arrow-up
                            </v-icon>
                            Local Storage
                          </v-tab>
                          <v-tab class="mr-3" small>
                            <v-icon class="mr-2">
                              mdi-code-tags
                            </v-icon>
                            URL
                          </v-tab>
                          <v-tab>
                            <v-icon class="mr-2" small>
                              fa-solid fa-server
                            </v-icon>
                            Server
                          </v-tab>
                        </v-tabs>
                      </div>
                    </template>
                    <v-tabs-items
                      v-model="tabs"
                        background-color="transparent"
                      >
                        <v-tab-item
                          :key="1"
                          class="p-5 rounded-xl border border-rose-600 border-dashed"
                        >
                          <v-card-text>
                            <template v-bind="1">
                              <!-- <form action="#" method="POST" enctype="multipart/form-data"> -->
                              <div class="grid">
                                <div class="text-4xl font-semibold text-rose-600 text-center">File Upload :</div>
                                <div class="text-4xl text-rose-600 grid place-items-center mt-5" >
                                  <div class="grid" @click="upmath"><button ><i class="fa-brands fa-cloudsmith"></i></button>
                                    <i class="text-xs text-gray-300">Click here(?)</i>
                                    <input type="file" class="hidden" accept="application/json, text/plain" id="m" ></div>
                                    
                                    <button class="v absolute text-xs text-green-500 font-light hidden translate-y-10"><i class="fa-solid fa-file-shield"></i> <span></span></button>
                                </div>
                                <div class="mt-5 flex justify-between mx-9">
                                  <button @click="paste" class="py-2 px-4 bg-rose-600 hover:bg-rose-800 save focus:ring-blue-500 focus:ring-offset-blue-200 w-full transition ease-in duration-200 text-white text-center text-base font-semibold shadow-md focus:outline-none focus:ring-2 focus:ring-offset-2  rounded-lg ">Upload</button>
                                </div>
                              </div>
                            </template>
                          </v-card-text>
                        </v-tab-item>
                        <v-tab-item
                          :key="2"
                        >
                        <v-card-text>
                            <template v-bind="1">
                              <v-row
                                align="center"
                                class="flex-nowrap"
                              >
                                <v-col
                                  cols="2"
                                >
                                <v-icon
                                  class="mx-1"
                                  >
                                    mdi-attachment
                                  </v-icon>
                                </v-col>
                                <v-col
                                  class="font-weight-light"
                                  cols="8"
                                >
                                <v-text-field 
                                  v-model="grabdom"
                                  label="Enter a domain address"
                                  @keypress.enter="grap()"
                                />
                                </v-col>

                                <v-col
                                  cols="auto"
                                  class="text-right"
                                >
                                <v-icon 
                                    class="mx-1"
                                    color="success"
                                    @click="grap()"
                                  >
                                    fa-solid fa-check
                                  </v-icon>
                                </v-col>
                              </v-row>
                            </template>
                        </v-card-text>
                        </v-tab-item>
                        <v-tab-item
                          :key="3"
                        >
                        <v-card-text>
                            <template v-for="lock in pos">
                              <v-row
                                :key="lock.id"
                                align="center"
                                class="flex-nowrap text-gray-800"
                              >

                                <v-col
                                  class="font-weight-light"
                                  cols="auto"
                                  v-text="lock.id"
                                />
                                <v-col
                                  class="font-weight-light"
                                  cols="7"
                                >
                                  <v-icon
                                  class="mx-1"
                                  >
                                    mdi-attachment
                                  </v-icon>
                                  {{ lock.name }}
                                </v-col>
                                <v-col
                                  cols="auto"
                                  class="text-right"
                                >
                                size: 
                                {{ lock.size }}
                                Bytes
                                </v-col>
                                <v-col
                                  cols="auto"
                                  class="text-right"
                                >
                                  <v-icon 
                                    class="mx-1"
                                    color="success"
                                    @click="link()"
                                  >
                                    fa-solid fa-check
                                  </v-icon>

                                </v-col>
                              </v-row>
                            </template>
                        </v-card-text>
                        </v-tab-item>
                        </v-tabs-items>
                </material-card>
              </div>
              <div class="text-h4 font-weight-light">
                Statistik pembayaran
              </div>
              <div class="text-caption">
                Last Student on <span >{{timex}}</span>
              </div>
            </div>
          </template>
          <v-card-text>
            <v-data-table
              item-key="name"
              :headers="headers"
              :items="lock"
              :search="search"
              :custom-filter="fuck"
            >
            <template v-slot:top>
             <v-row>
              <v-col
                cols="12"
                md="8"
              >
                <v-text-field
                  v-model="search"
                  label="Search name here..."
                  filled
                  append-icon="fa-solid fa-magnifying-glass"
                  class="mx-1"
                ></v-text-field>
              </v-col>
              <v-col 
                cols="auto"
                md="4"
              >
                <div class=" grid grid-cols-3 mx-1">
                  <v-btn
                  color="primary"
                  class="mx-1"
                  @click="nw"
                  >
                    <v-icon
                      small
                      class="mx-1"
                    >
                      fa-solid fa-plus
                    </v-icon>
                    <span class="font-bold text-xs">New</span>
                  </v-btn>
                    <v-btn
                    color="secondary"
                    class="mx-1"
                    @click="imp"
                  >
                    <v-icon
                      small
                      class="mx-1"
                    >
                      fa-solid fa-cloud-arrow-up
                    </v-icon
                    >
                    <span class="font-bold text-xs">Import</span>
                  </v-btn>
                  <v-btn
                    color="info"
                    class="mx-1"
                    @click="down"
                  >
                    <v-icon
                      small
                      class="mx-1"
                    >
                      fa-solid fa-download
                    </v-icon>
                    <span class="font-bold text-xs">Download</span>
                  </v-btn>
                </div>
                    
              </v-col>
             </v-row>
            </template>
            <template v-slot:item.action="{ item }">
            <v-icon
              small
              class="mr-2 edit"
              @click="edit(item)"
            >
              mdi-pencil
            </v-icon>
            <v-icon
              small
              class="mr-2 del"
              @click="delet(item)"
            >
              mdi-delete
            </v-icon>
            <v-icon
              small
              class="mr-2 sv"
              @click="save(item)"
            >
            fa-solid fa-floppy-disk
            </v-icon>
            </template>
            <template v-slot:item.activity="{ item }">
              <v-chip
                :color="getColor(item.activity)"
                class="z-10"
                dark
              >
                {{ item.activity }}
              </v-chip>
            </template>
            <template v-slot:item.LP="{ item }">
              <v-chip
                :color="getCalor(item.LP)"
                class="z-10"
                dark
              >
                {{ item.LP }}
              </v-chip>
            </template>
            <template v-slot:item.action="{ item }">
              <v-icon
              small
              class="mr-2 edit"
              @click="edit(item)"
            >
              mdi-pencil
            </v-icon>
            <v-icon
              small
              class="mr-2 del"
              @click="delet(item)"
            >
              mdi-delete
            </v-icon>
            <v-icon
              small
              class="mr-2 sv"
              @click="save(item)"
            >
            fa-solid fa-floppy-disk
            </v-icon>
            </template>
            </v-data-table>
          </v-card-text>
        </material-card>
      </v-col>
    </v-row>
  </v-container>
</template>
<script>
import Vue from 'vue'
  const lineSmooth = Vue.chartist.Interpolation.cardinal({
    tension: 0.2,
  })
export default {
  data: () => {
    return {
      timex: '',
      search: '',
      tabs: 0,
      tasks: {
        0: [
          {
            text: 'Sign contract for "What are conference organizers afraid of?"',
            value: true,
          },
          {
            text: 'Lines From Great Russian Literature? Or E-mails From My Boss?',
            value: false,
          },
          {
            text: 'Flooded: One year later, assessing what was lost and what was found when a ravaging rain swept through metro Detroit',
            value: false,
          },
          {
            text: 'Create 4 Invisible User Experiences you Never Knew About',
            value: true,
          },
        ],
        1: [
          {
            text: 'Flooded: One year later, assessing what was lost and what was found when a ravaging rain swept through metro Detroit',
            value: true,
          },
          {
            text: 'Sign contract for "What are conference organizers afraid of?"',
            value: false,
          },
        ],
        2: [
          {
            text: 'Lines From Great Russian Literature? Or E-mails From My Boss?',
            value: false,
          },
          {
            text: 'Flooded: One year later, assessing what was lost and what was found when a ravaging rain swept through metro Detroit',
            value: true,
          },
          {
            text: 'Sign contract for "What are conference organizers afraid of?"',
            value: true,
          },
        ],
      },
      stats: [
          {
            actionIcon: 'mdi-alert',
            actionText: 'Get More Space...',
            color: 'success',
            icon: 'mdi-account-group',
            title: 'Already paid',
            value: '184 murid',
          },
          {
            actionIcon: 'mdi-tag',
            actionText: 'Tracked from Google Analytics',
            color: 'primary',
            icon: 'mdi-account-group',
            title: 'Unpaid',
            value: '100 murid',
          },
          {
            actionIcon: 'mdi-calendar-range',
            actionText: 'Last 24 Hours',
            color: '#FD9A13',
            icon: 'fa-solid fa-coins',
            title: 'Paid',
            value: '$121',
          },
          {
            actionIcon: 'mdi-history',
            actionText: 'Just Updated',
            color: 'info',
            icon: 'fa-solid fa-file-invoice',
            title: 'Not yet paid',
            value: '$245',
          },
        ],
      headers: [
        {
          sortable: true,
          text: 'ID',
          value: 'id',
        },
        {
          sortable: true,
          text: 'Name',
          value: 'name',
        },
        {
          sortable: true,
          text: 'Last Payment',
          value: 'LP',
        },
        {
          sortable: false,
          text: 'PAY',
          value: 'tagihan',
        },
        {
          sortable: true,
          text: 'Activity',
          value: 'activity',
        },
        {
          sortable: false,
          text: 'Action',
          value: 'action',    
        },
        ],
      items: [
          {
            name: 'Frozen Yogurt',
            LP: '20/10/2022',
            tagihan: '$455',
            activity: 'Lunas',
          },
          {
            name: 'Ice cream sandwich',
            LP:' 20/10/2022',
            tagihan: '$522',
            activity: 'Lunas',
          },
          {
            name: 'Eclair',
            LP:' 20/10/2022',
            tagihan: '$522',
            activity: 'Belum lunas',
          },
          {
            name: 'Cupcake',
            LP:' 20/10/2022',
            tagihan: '$522',
            activity: 'Lunas',
          },
          {
            name: 'Gingerbread',
            LP:' 20/10/2022',
            tagihan: '$322',
            activity: 'Belum lunas',
          },
          {
            name: 'Jelly bean',
            LP:'20/10/2022',
            tagihan: '$111',
            activity: 'Lunas',
          },
          {
            name: 'Lollipop',
            LP:' 20/10/2022',
            tagihan: '$433',
            activity: 'Lunas',
          },
          {
            name: 'Honeycomb',
            LP: '20/10/2022',
            tagihan: '$142',
            activity: 'Lunas',
          },
          {
            name: 'Donut',
            LP: '20/10/2022',
            tagihan: '$353',
            activity: 'Lunas',
          },
          {
            name: 'KitKat',
            LP: '20/10/2022',
            tagihan: '$200',
            activity: 'Belum lunas',
          },
        ],
      lock : [],
      gap : [],
      pos : [],
      grabdom: [],
      edit (item){
        this.editedIndex = this.gap.indexOf(item)
        this.editedItem = Object.assign({}, item)
        // this.dialog = true
          var x = document.querySelectorAll("tbody")[0].querySelectorAll("tr")
          x[item.id - 1].contentEditable = "true"
      },
      save (item){
          this.editedIndex = this.items.indexOf(item)
          this.editedItem = Object.assign({}, item)
          // this.dialogDelete = true
          // Start collection
          var x = document.querySelectorAll("tbody")[0].querySelectorAll("tr")
            var y = x[item.id - 1].querySelectorAll("td")
            var m = y[1].innerText
            if (this.items[item.id - 1].name !== m){
              this.items[item.id - 1].name = y[1].innerText
              this.items[item.id - 1].LP = y[2].innerText
              this.items[item.id - 1].tagihan = y[3].innerText
              var c = item.id
            setTimeout(() => {
              x[item.id - 1].contentEditable = "false"
              alert("ID : " + c +" saved")
            }, 300)
            }else if(this.items[item.id - 1].tagihan !== y[3].innerText){
              this.items[item.id - 1].name = y[1].innerText
              this.items[item.id - 1].LP = y[2].innerText
              this.items[item.id - 1].tagihan = y[3].innerText
              var c = item.id
            setTimeout(() => {
              x[item.id - 1].contentEditable = "false"
              alert("ID : " + c +" saved")
            }, 300)
            }else{
              x[item.id -1].contentEditable = "false"
            }
      },
      delet (item){
        this.editedIndex = this.gap.indexOf(item)
        this.editedItem = Object.assign({}, item)
        // this.dialogDelete = true
        var x = document.querySelectorAll("tbody")[0].querySelectorAll("tr")
        x[item.id - 1].style.display = "none"
      },
    }
  },
  created(){
    this.items.forEach((item, i ) => {
      this.items[i].id = i + 1
    })
    var y = localStorage.getItem("payment")
    if (y == null) {
      localStorage.setItem("payment", JSON.stringify(this.items))
      this.lock = this.items
      this.lock.forEach((item, i) => {
      this.gap[i++] = item.name
    })
    }else{
      this.lock = JSON.parse(y)
      this.lock.forEach((item, i) => {
      this.gap[i++] = item.name
    })
    }
    var date = new Date()
     var month = ["January","February","March","April","May","June","July","August","September","October","November","December"]
     this.timex = date.getDate() +'th '+ month[date.getMonth()]+ ', '+ date.getFullYear()
  },
  methods: {
    getColor(activity){
      if (activity == 'Lunas') return 'primary'
      else if (activity == 'Belum lunas') return 'red'
      else return 'gray'
    },
    getCalor(LP){
      return 'green'
    },
    fuck(value, search, item) {
      return value != null &&
        search != null &&
        typeof value === 'string' &&
        value.toString().toLocaleLowerCase().indexOf(search) !== -1
    },
    nw(){
      const mk = document.querySelectorAll(".mvp")[0]
      if (mk.style.display == "none") {
        mk.style.display = "block"
      } else {
        mk.style.display = "none"
      }  
    },
    imp(){
      const l = document.querySelectorAll('.g')[0]
      if (l.style.display == "none") {
        l.style.display = "block"
      } else {
        l.style.display = "none"
      }
    },
    down(){
      var v = JSON.stringify(this.gap)
      var nv = new Blob([v])
      var y = document.createElement("a")
      y.href = window.URL.createObjectURL(nv);
      y.download = "fakes.json"
      y.click()
    },
    upmath(){
      var r = document.getElementById("m") 
      var z = document.querySelectorAll('.v')[0]
      r.click()
      r.addEventListener("input", () => {
        if (r.files.length == 1) {
          z.querySelectorAll("span")[0].innerText = r.files[0].name
          z.style.display = "block"
        }
      })
      
    },
    paste(){
      var x = document.getElementById("m")
      var y = x.files[0]
      var l = []
      var query = [
        {
          'id': this.pos.length + 1,
          'name': y.name,
          'lastModiiedf': y.lastModified,
          'lastModifiedDate': y.lastModifiedDate,
          'size': y.size,
          'type': y.type
        }
      ]
      // Save
      this.pos = this.pos.concat(query)
      var sav = localStorage.setItem("file", JSON.stringify(this.pos))
      if (sav == false){
        alert("err")
      }else{
        alert("Anda tidak bisa mengupload file di versi demo")
      }
      // SET
      // var fromd = new FormData()
      // fromd.append("file", y)
      // fetch('http://localhost:8080/#m[0]file/', {
      //   method: 'post',
      //   body: fromd
      // })
      // .then((response) => response.json())
      // .then((result) => {
      //   console.log('Success:', result);
      // })
      // .catch((error) => {
      //   console.error('Error:', error);
      // });
    },
    grap(){
      let q = []
      const m = this.grabdom
      const d = new Date()
      fetch(m)
      .then(res => res.json())
      .then(json => {
        var y = json.results
        // y.forEach((item, i) => {
        //   var set = [
        //   {
        //     'name': y[i++].name.first + ' ' + y[i++].name.last,
        //     'email': y[i++].email,
        //     'uid': y[i++].uid,
        //     'kelas': y[i++].kelas,
        //   },
        // ]
        // this.data = set
        // })
        const d = new Date()
        var yg = this.pos.length + 1
      
      var query = [
        {
          'id': yg,
          'name': m,
          'lastModiiedf': y.lastModified,
          'lastModifiedDate': d,
          'size': this.pos[0].size * yg,
          'type': "application/json"
        }
      ]
      // Save
      this.pos = this.pos.concat(query)
      })
    },
    ssclose(){
      var mn = document.querySelectorAll(".mvp")[0]
      if(mn.style.display == "none"){
        mn.style.display = "block"
      }else{
        mn.style.display = "none"
      }
    },
    sssclose(){
      var mn = document.querySelectorAll(".g")[0]
      if(mn.style.display == "none"){
        mn.style.display = "block"
      }else{
        mn.style.display = "none"
      }
    },
    svbtn(){
      if(this.name.length > 0){
          if ((this.name && this.tagihan) == undefined) {
            alert("Isi bagian yang kosong")
          }else{
          this.name.forEach((item, i) => {
           var bad = this.name[i]
           var mat = this.lock
           function box(ma) {
            if (ma.name == bad) {
              return ma.name == bad
            }
           }
           var gc = mat.filter(box)
           var tgl = () => {
            var th = new Date()
            if (th.getDate() < 10) {
              return '0'+ th.getDate()
            }else{
              return th.getDate()
            }
           }
           var x = new Date()
           if (this.tagihan <= 222) {       
            var mc =  Array.from({length: gc.length},(_, i) => gc[i++].activity = 'Belum Lunas')
           } else {              
            var mc =  Array.from({length: gc.length},(_, i) => gc[i++].activity = 'Lunas')
           }
           var dc =  Array.from({length: gc.length},(_, i) => gc[i++].tagihan = '$' + this.tagihan)               
           var tq =  Array.from({length: gc.length},(_, i) => gc[i++].LP = tgl()+'/'+x.getMonth()+'/'+x.getFullYear())   
          })
          setTimeout(() => {
            localStorage.setItem("payment", JSON.stringify(this.items))

            window.location.reload(true)
          }, 3000)
          }
        }else if(this.name == undefined){
          alert("Isi bagian yang kosong")
        }
    },
  },
  mounted(){
     
  }
}
</script>